      MainController.prototype.uiOnParamsChanged = function(changedParams, transition) {
        return this.topic = changedParams.topic;
      };

      MainController.prototype.resetCountryAlpha2 = function() {
        this._countryAlpha2 = [];
        return this.buildFilteredLegislatures();
      };

      MainController.prototype.resetDifficultyLevel = function() {
        this._difficultyLevel = [];
        return this.buildFilteredLegislatures();
      };

      MainController.prototype.resetTerm = function() {
        this._term = [];
        return this.buildFilteredLegislatures();
      };

      MainController.prototype.hasCountryAlpha2 = function() {
        return this._countryAlpha2.length;
      };

      MainController.prototype.hasDifficultyLevel = function() {
        return this._difficultyLevel.length;
      };

      MainController.prototype.hasTerm = function() {
        return this._term.length;
      };

      MainController.prototype.getCountryAlpha2 = function() {
        return this._countryAlpha2;
      };

      MainController.prototype.getDifficultyLevel = function() {
        return this._difficultyLevel;
      };

      MainController.prototype.getTerm = function() {
        return this._term;
      };

      MainController.prototype.isCountryAlpha2 = function(val) {
        return this._countryAlpha2.indexOf(val) > -1;
      };

      MainController.prototype.isDifficultyLevel = function(val) {
        return this._difficultyLevel.indexOf(val) > -1;
      };

      MainController.prototype.isTerm = function(val) {
        return this._term.indexOf(val) > -1;
      };

      MainController.prototype.matchCountryAlpha2 = function(val) {
        return _.filter(this.getFilteredLegislatures(), function(l) {
          return l.country.alpha2 === val;
        });
      };

      MainController.prototype.matchDifficultyLevel = function(val) {
        return _.filter(this.getFilteredLegislatures(), {
          difficulty_level: val
        });
      };

      MainController.prototype.matchTerm = function(val) {
        return _.filter(this.getFilteredLegislatures(), {
          term: val
        });
      };

      MainController.prototype.someCountryAlpha2 = function(val) {
        return _.some(this.getFilteredLegislatures(), function(l) {
          return l.country.alpha2 === val;
        });
      };

      MainController.prototype.someDifficultyLevel = function(val) {
        return _.some(this.getFilteredLegislatures(), {
          difficulty_level: val
        });
      };

      MainController.prototype.someTerm = function(val) {
        return _.some(this.getFilteredLegislatures(), {
          term: val
        });
      };

      MainController.prototype.toggleCountryAlpha2 = function(val) {
        return this.toggleFrom(this._countryAlpha2, val);
      };

      MainController.prototype.toggleDifficultyLevel = function(val) {
        return this.toggleFrom(this._difficultyLevel, val);
      };

      MainController.prototype.toggleTerm = function(val) {
        return this.toggleFrom(this._term, val);
      };

