(function() {
  angular.module('politicalGaps').config(function($stateProvider, $urlRouterProvider) {
    'ngInject';
    $urlRouterProvider.otherwise('/');
    $stateProvider.state('main', {
      url: '/',
      templateUrl: 'app/main/main.html',
      controller: 'MainController',
      controllerAs: 'main',
      resolve: {
        countries: function($http) {
          'ngInject';
          return $http.get('data/countries.json').then(function(r) {
            return r.data;
          });
        },
        legislatures: function($http, countries) {
          'ngInject';
          return $http.get('data/legislatures.json').then(function(r) {
            var i, legislature, len, ref;
            ref = r.data;
            for (i = 0, len = ref.length; i < len; i++) {
              legislature = ref[i];
              legislature.end_date = new Date(legislature.end_date);
              legislature.start_date = new Date(legislature.start_date);
              legislature.country = _.find(countries, {
                alpha2: legislature.country
              });
              legislature.term = (legislature.start_date.getFullYear()) + "-" + (legislature.end_date.getFullYear());
              legislature.display_name = legislature.name;
              legislature.display_name += ' - ';
              legislature.display_name += 'Completion rate: ';
              legislature.display_name += ~~(legislature.completion * 1000) / 10;
              legislature.display_name += '%';
            }
            return r.data;
          });
        }
      }
    });
    return $stateProvider.state('main.chart', {
      url: ':legislature_id_eq',
      templateUrl: 'app/main/chart/chart.html',
      controller: 'MainChartController',
      controllerAs: 'chart',
      params: {
        topic: {
          dynamic: true,
          value: 'gender'
        }
      },
      resolve: {
        legislature_id: function($stateParams) {
          'ngInject';
          return 1 * $stateParams.legislature_id_eq;
        },
        legislature: function(legislatures, legislature_id) {
          'ngInject';
          return _.find(legislatures, {
            id: legislature_id
          });
        },
        summary: function($http, legislature_id) {
          'ngInject';
          return $http.get('data/summary-' + legislature_id + '.json').then(function(r) {
            return r.data;
          });
        }
      }
    });
  });

}).call(this);



