angular.module("politicalGaps").run(["$templateCache", function($templateCache) {$templateCache.put("app/main/main.html","<div class=main><form class=main__elastic ng-show=\"\'main\' | isState\"><div class=main__elastic__introduction><div class=container><h1>Political Gaps</h1><p class=lead>PoliticalGaps is the first investigation on <a href=http://www.jquestapp.com target=_blank>jQuest</a>, a platform for journalism schools that lets journalism students in Europe learn datajournalism, connect with one another and investigate collaboratively.</p><p class=lead>We are investigating the representativity of elected officials in assemblies all over Europe. The data you see here is live. The database will grow more complete as the academic year continues.</p></div></div><div class=main__elastic__filters><div class=container><h3 class=main__elastic__filters__heading>Filter legislatures</h3><div class=hidden-md-up><div class=form-group><label for=input-country>By country</label><select ng-options=\"country.alpha2 as country.name for country in ::main.countries\" ng-model=\"main.gs(\'countryAlpha2\')\" ng-model-options=\"{ getterSetter: true }\" class=form-control id=input-country><option value=\"\">All</option></select></div><div class=form-group><label for=input-difficulty-level>By administrative levels</label><select ng-options=\"level as main.administrativeLevels[level - 1] for level in ::main.difficultyLevels\" ng-model=\"main.gs(\'difficultyLevel\')\" ng-model-options=\"{ getterSetter: true }\" class=form-control id=input-difficulty-level><option value=\"\">All</option></select></div><div class=form-group><label for=input-term>By term</label><select ng-options=\"term as term for term in ::main.terms\" ng-model=\"main.gs(\'term\')\" ng-model-options=\"{ getterSetter: true }\" class=form-control id=input-term><option value=\"\">All</option></select></div></div><div class=\"row hidden-sm-down\"><div class=col-sm-4><div class=main__elastic__filters__card><h4 class=main__elastic__filters__title><button class=\"btn btn-link btn-sm pull-xs-right\" ng-click=main.resetCountryAlpha2() ng-if=main.hasCountryAlpha2()>All</button> Country</h4><ul class=\"list-unstyled main__elastic__list main__elastic__list--country\"><li ng-repeat=\"country in ::main.countries\"><a class=\"btn-block main__elastic__list__item\" ng-click=main.toggleCountryAlpha2(country.alpha2) ng-class=\"{\n                            \'main__elastic__list__item--active\': main.isCountryAlpha2(country.alpha2),\n                            \'main__elastic__list__item--reduced\': !main.someCountryAlpha2(country.alpha2)\n                          }\"><span class=\"tag pull-xs-right\">{{ main.matchCountryAlpha2(country.alpha2).length }} </span>{{ country.name }}</a></li></ul></div></div><div class=col-sm-4><div class=main__elastic__filters__card><h4 class=main__elastic__filters__title><button class=\"btn btn-link btn-sm pull-xs-right\" ng-click=main.resetDifficultyLevel() ng-if=main.hasDifficultyLevel()>All</button> Administrative level</h4><ul class=\"list-unstyled main__elastic__list main__elastic__list--administrative-level\"><li ng-repeat=\"difficultyLevel in ::main.difficultyLevels\"><a class=\"btn-block main__elastic__list__item\" ng-click=main.toggleDifficultyLevel(difficultyLevel) ng-class=\"{\n                            \'main__elastic__list__item--active\': main.isDifficultyLevel(difficultyLevel),\n                            \'main__elastic__list__item--reduced\': !main.someDifficultyLevel(difficultyLevel)\n                          }\"><span class=\"tag pull-xs-right\">{{ main.matchDifficultyLevel(difficultyLevel).length }} </span>{{ main.administrativeLevels[difficultyLevel - 1] }}</a></li></ul></div></div><div class=col-sm-4><div class=main__elastic__filters__card><h4 class=main__elastic__filters__title><button class=\"btn btn-link btn-sm pull-xs-right\" ng-click=main.resetTerm() ng-if=main.hasTerm()>All</button> Term</h4><ul class=\"list-unstyled main__elastic__list main__elastic__list--term\"><li ng-repeat=\"term in main.terms | filter:main.isRecentTerm | filter:main.someTerm\"><div class=main__elastic__list__title ng-if=$first>Recent</div><a class=\"btn-block main__elastic__list__item\" ng-click=main.toggleTerm(term) ng-class=\"{\n                            \'main__elastic__list__item--active\': main.isTerm(term)\n                          }\"><span class=\"tag pull-xs-right\">{{ main.matchTerm(term).length }} </span>{{ term }}</a></li><li ng-repeat=\"term in main.terms | filter:main.isOldTerm | filter:main.someTerm\"><div class=main__elastic__list__title ng-if=$first>Old</div><a class=\"btn-block main__elastic__list__item\" ng-click=main.toggleTerm(term) ng-class=\"{\n                            \'main__elastic__list__item--active\': main.isTerm(term)\n                          }\"><span class=\"tag pull-xs-right\">{{ main.matchTerm(term).length }} </span>{{ term }}</a></li><li ng-repeat=\"term in main.terms | filter:main.isHistoricalTerm | filter:main.someTerm\"><div class=main__elastic__list__title ng-if=$first>Historical</div><a class=\"btn-block main__elastic__list__item\" ng-click=main.toggleTerm(term) ng-class=\"{\n                            \'main__elastic__list__item--active\': main.isTerm(term)\n                          }\"><span class=\"tag pull-xs-right\">{{ main.matchTerm(term).length }} </span>{{ term }}</a></li></ul></div></div></div></div><div class=main__elastic__filters__alert><div class=container><i class=\"fa fa-filter\"></i> There is {{ main.getFilteredLegislatures().length }} results that match with your filters.</div></div></div><div class=container><ul class=list-unstyled><li ng-repeat=\"legislature in main.getFilteredLegislatures()\" class=main__elastic__card ui-sref=\"main.chart({ legislature_id_eq: legislature.id })\"><div class=\"pull-sm-right hidden-sm-down\"><button class=\"btn btn-link btn-outline\">Visualize representativity</button></div><h5>{{ legislature.name_english }}</h5><p>{{ legislature.country.name }} - {{ legislature.term }} - Completed at {{ legislature.completion * 100 | number:0 }}%</p><div class=main__elastic__card__progress><div class=main__elastic__card__progress__bar ng-style=\"{ width: legislature.completion * 100 + \'%\' }\"></div></div></li></ul></div></form><div ui-view></div></div>");
$templateCache.put("app/main/chart/chart.html","<div class=\"text-center main__heading\"><a class=main__heading__filters ui-sref=main><i class=\"fa fa-caret-down fa-fw\"></i> See the representativity of another assembly</a><div class=main__heading__title><h3>{{ chart.legislature.name_english }}</h3><p class=\"lead m-b-0\">{{ chart.legislature.country.name }} - {{ chart.legislature.term }} - Completed at {{ chart.legislature.completion * 100 | number:0 }}%</p></div><ul class=\"nav nav-pills main__heading__tabs\"><li class=nav-item><a class=nav-link ui-sref=\"main.chart({topic: \'gender\'})\" ui-sref-active=active>Gender</a></li><li class=nav-item><a class=nav-link ui-sref=\"main.chart({topic: \'age_range\'})\" ui-sref-active=active>Age</a></li><li class=nav-item><a class=nav-link ui-sref=\"main.chart({topic: \'profession_category\'})\" ui-sref-active=active>Occupation</a></li><li class=nav-item><a class=nav-link ui-sref=\"main.chart({topic: \'political_leaning\'})\" ui-sref-active=active>Political leaning</a></li></ul></div><div class=\"main__chart main__chart--{{ chart.topic }}\" main-chart summary=chart.summary topic=chart.topic></div>");}]);